.box {
}

.row {
  margin: -12px;
  display: flex;
  flex-wrap: wrap;
}

.col {
  padding: 12px;
  flex: 0 0 33.3333%;
  max-width: 33.3333%;
}

.item {
  background: #ffffff;
  box-shadow: 0px 12px 24px rgba(87, 111, 172, 0.08);
  border-radius: 12px;
  overflow: hidden;
  display: block;
  text-decoration: none;
  height: 100%;

  &:hover {
    .title {
      color: #fab62f;
    }
  }
}

.head {
  font-size: 0;
  background-size: cover;
  background-repeat: no-repeat;

  &:before {
    content: '';
    display: block;
    padding-top: 68.6%;
  }

  img {
    max-width: 100%;
  }
}
.metrics {
  margin: -25px 16px 0;
  background: #ffffff;
  box-shadow: 0px 4px 12px rgba(87, 111, 172, 0.05);
  border-radius: 4px;
  z-index: 1;
  position: relative;
  padding: 18px;
  display: flex;
  justify-content: center;
}
.metricItem {
  display: flex;
  margin-right: 54px;
  align-items: center;

  &:last-child {
    margin-right: 0;
  }
}
.metricLabel {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  color: #64697a;
  margin-right: 8px;
  text-transform: uppercase;
}
.metricValue {
  font-size: 12px;
  line-height: 15px;
  color: #1e0e33;
  padding: 2px 6px;
  background: #ffeec2;
  border-radius: 4px;
}
.metricValueGray {
  background: #ebedf5;
}

.body {
  padding: 20px 24px 24px;
}

.title {
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #1e0e33;
  margin-bottom: 16px;
  transition: 0.3s;
}

.descr {
  font-size: 14px;
  line-height: 17px;
  color: #64697a;
}
